<template>
  <div class="file-container">
    <el-form>
      <el-upload
        :limit="limit"
        action
        accept="image/*"
        :on-change="uploadFile"
        list-type="picture-card"
        :auto-upload="false"
        :file-list="fileList"
        :on-exceed="handleExceed"
        :on-preview="handlePictureCardPreview"
        :on-remove="handleRemove"
        :on-success="handlesuccess"
        ref="upload"
        class="avatar-uploader"
        :class="{ hide: showUpload }"
        :disabled="disabled"
      >
        <i class="el-icon-plus"></i>
      </el-upload>
      <el-dialog width="30%" :visible.sync="dialogVisible">
        <img width="100%" :src="imgUrl.url" alt />
      </el-dialog>
    </el-form>
  </div>
</template>

<script>
export default {
  name: "UpLoadImg",
  props: {
    limit: Number,
    fileList: Array,
    disabled: Boolean,
  },
  data() {
    return {
      showUpload: false, //limit 
      dialogVisible: false, //
      imgUrl: [], //
    };
  },
  watch:{
      fileList(newName,oldName){
          if (newName.length == this.limit) this.showUpload = true;
            else this.showUpload = false;
      }
  },
  methods: {
      //
      handleRemove(file,flieList){
          const index = this.fileList.findIndex(item=>{
              item ===file.uid
          });
          this.imgUrl.splice(index,1);
          // this.$emit('delUrl',this.imgUrl);
          if(this.fileList.length<this.limit){
              this.showUpload = false
          }
      },
      //
      handlePictureCardPreview(file){
          this.imgUrl.url = file.url;
          this.dialogVisible = true;
      },
    //   
    submitUpload(){
        // submit
        this.$refs.upload.submit();
    },
    handlesuccess(res,f,fl){
      console.log(res);
    },
    // 
    uploadFile(e,flieList){
        //
        if(flieList.length>=this.limit){
            this.showUpload = true;
        };
        const file = e.raw;
        const size = file.size /1024/1024/2;
        if(
            !(
                file.type ==='image/png'||
                file.type ==='image/gif'||
                file.type ==='image/jpg'||
                file.type ==='image/jpeg'
            )
        ){
            this.$message({
                message:'pnggifjpgjpeg',
                type:'error',
                center:true
            })
        }
        else if (size>2) {
                this.$message({
                     message:'2M',
                     type:'error',
                     center:true
                })
            
        }
        else{
            if(this.limit==1){
                this.imgUrl = [];
            };
            const params = new FormData();
            params.append("source",file);
            //
            // uploadImg(params).then(res =>{
            //     if(res.data.status ===1){
            //         this.$message({
            //             message:'',
            //             type:'success',
            //             center:ture
            //         });
            //         this.imgUrl = res.data
            //         this.$emit('getUrl',this.imgUrl)
            //     }
            //     else{
            //         this.$message({
            //             message:'',
            //             type:'error',
            //             center:true
            //         })
            //     }
            // })
        }
    },
    //
    handleExceed(file,fileList){
        this.$message({
            message:`${this.limit}`
        })
    }
  },
};
</script>

<style scoped>
.file-container {
  width: 100%;
}
.hide .el-upload--picture-card {
  display: none !important;
}
.avatar-uploader > .el-upload {
  width: 200px;
  height: 200px;
  line-height: 200px;
  border-radius: 0px;
  background: #fff;
  border: 1px dashed #ccc;
}
.avatar-uploader > .el-upload > i {
  font-size: 28px;
  color: #ccc;
}
.avatar-uploader > .el-upload-list {
  display: block;
}
.avatar-uploader > .el-upload-list > .el-upload-list__item {
  width: 200px;
  height: 200px;
  display: block;
}
.avatar-uploader > .el-upload-list > .el-upload-list__item > img {
  width: 200px;
  height: 200px;
  border-radius: 0px;
}
</style>