// import request from "./api/request";
import _import from './router/_import_component'
import Layout from './pages/Home.vue'
import router from "./router/index";
import store from './store/index';
import Global from "./global/global"
import request from './api/request';
var getRouter;
let flag
let asyncroute = [
    {
        path: '/',
        name: 'home',
        redirect: '/home',
        component: "Layout",
        meta: { isAurth: true, title: "", icon: "el-icon-menu" },
        children: [
            //homepage 
            {
                name: 'page',
                path: '/home',
                component: "/Explain",
                meta: { title: "" },

            },

        ],
    },
    //document 
    {
        path: '/document',
        redirect: '/document/index',
        component: "Layout",
        meta: { title: "", icon: "el-icon-notebook-1" },
        children: [
            {
                path: '/document/index',
                component: "/Document",
                meta: { title: "" },
            },
        ],
    },
    // icon 
    {
        path: '/icon',
        component: "Layout",
        redirect: '/icon/index',
        meta: { title: "", icon: 'el-icon-s-opportunity' },
        children: [
            {
                path: '/icon/index',
                component: "/Icon",
                meta: { title: "" },
            },
        ],
    },
    // permission 
    {
        name: 'permission',
        path: '/permission',
        meta: { title: "", icon: "el-icon-s-custom" },
        component: "Layout",
        children: [
            // directive \
            {
                path: '/permission/directive',
                component: "/Permission/Directive",
                meta: { title: "" },

            },
        ]
    },
    //
    //  components 
    {
        name: 'components',
        path: '/components',
        component: "Layout",
        meta: { title: "", icon: "el-icon-bangzhu" },
        children: [
            // first \1
            {
                name: 'upload',
                path: '/components/upload',
                component: "/Upload",
                meta: { title: "" },

            },
            // second \2
            {
                name: 'download',
                path: '/components/download',
                component: "/DownLoad",
                meta: { title: "" },

            },
            // third \3
            {
                name: 'tinymce',
                path: '/components/tinymce',
                component: "/QuillEditor",
                meta: { title: "" },

            },
        ]
    },
    //Table
    {
        name: "Table",
        path: '/charts',
        component: "Layout",
        meta: { title: "", icon: "el-icon-tickets" },
        children: [
            // Tableone \1
            {
                name: 'Tableone',
                path: '/charts/Tableone',
                component: "/Table",
                meta: { title: "" },

            },
            // Tabletwo \2
            {
                name: 'Tabletwo',
                path: '/charts/Tabletwo',
                component: "/Table",
                meta: { title: "2" },

            },
        ]
    },
]
let roleRoute = {
    path: '/permission/role',
    component: "/Permission/role",
    meta: { title: "" },

}
//
router.beforeEach((to, from, next) => {
    if (!getRouter) {
        //
        // 
        getRoute(to, next)
    }
    else {
        next();
    }
})
async function getRoute(to, next) {

    await request('/user', "GET").then(res => {
        if (!flag) {
            store.commit('user_Login/getRole', res.data.role)
            if (res.data.role === "admin") {
                asyncroute.forEach(item => {
                    if (item.name === "permission") {
                        item.children.push(roleRoute);
                    }
                })
            }
            flag = true
        }

    }, err => {

    })
    // vuex
    getRouter = asyncroute;
    console.log(getRouter);
    //localStorage
    routerGO(to, next);

}
function routerGO(to, next) {
    //
    getRouter = filterAsyncRoutes(getRouter);
    router.options.routes = getRouter;
    router.addRoutes(getRouter)
    Global.antRouter = getRouter
    next({ ...to, replace: true })
}
//
function filterAsyncRoutes(RouterMap) {
    const accessRouterMap = RouterMap.filter(route => {
        if (route.component) {
            if (route.component === 'Layout') {
                route.component = Layout;
            }
            else {
                route.component = _import(route.component)
            }
        }
        if (route.children && route.children.length) {
            route.children = filterAsyncRoutes(route.children)
        }
        return true
    })
    return accessRouterMap
}


