<template>
  <div id="app">
    <keep-alive>
      <router-view v-if="isRouterView"></router-view>
    </keep-alive>
  </div>
</template>

<script>
import request from "./api/request";
export default {
  name: "App",
  created() {
    //
    let loginPage = {
      name: "login",
      path: "/login",
      component: (resolve) => {
        require(["@/pages/Login"], resolve);
      },
      meta: { title: ""},
    };
    this.$router.addRoute(loginPage);
    this.isLogin();
  },
  data() {
    return {
      isRouterView: true,
    };
  },
  methods: {
    //
    isLogin() {
        if (this.$store.state.user_Login.role) {
          // 
          this.$router.push("/");
        } else {
          //
          this.$router.push({
            path: "/login",
          });
        }
    },
  },
};
</script>

<style>
* {
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
  color: inherit;
}
</style>
