<template>
  <div class="wrap">
    <el-card>
      <el-button type="primary" style="float:right" @click="handleAdd"></el-button>
      <el-table
        :data="tableData"
        :default-sort="{ prop: 'id', order: 'descending' }"
        border
        stripe
      >
        <template slot-scope="scope">
          <el-table-column
            prop="id"
            label=""
            width="180"
            sortable
          ></el-table-column>
          <el-table-column
            prop="date"
            label=""
            width="180"
          ></el-table-column>
          <el-table-column
            prop="title"
            label=""
            width="380"
            @click="handleEdit(scope)"
          >
          </el-table-column>
          <el-table-column
            prop="author"
            label=""
            width="180"
          ></el-table-column>
          <el-table-column label="" >
            <template slot-scope="scope">
              <el-button type="primary" @click="handleEdit(scope)"
                ></el-button
              >
              <el-button
                type="danger"
                @click="handleDelete(scope.$index, scope.row)"
                ></el-button
              >
            </template>
          </el-table-column>
        </template>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="query.pageNum"
        :page-sizes="[2, 3, 4, 5]"
        :page-size="query.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="query.total"
      ></el-pagination>
    </el-card>
    <EditItem :dialogVisible="dialogVisible"></EditItem>
  </div>
</template>
<script>
import EditItem from "./EditItem.vue";
export default {
  components: { EditItem },
  name: "List",
  data() {
    return {
      //table 
      tableData: [],
      //
      query: {
        pageNum: 1,
        pageSize: 3,
        total: 0,
      },
      //
      dialogVisible: false,
    };
  },
  mounted() {
    this.getData();
    //dialog
    this.$bus.$on("closeDialog", () => {
      this.dialogVisible = false;
    });
    //
    this.$bus.$on('renewTable',()=>{
        this.getData();
    })
  },
  beforeDestroy() {
    this.$bus.$off("closeDialog");
  },
  methods: {
    getData() {
      //table
      let DataAll = this.$store.state.bookTable.tableList;
      if (DataAll.length > 0) {
        //
        this.tableData = [];
        //
        let strlength = (this.query.pageNum - 1) * this.query.pageSize + 1;
        //
        let endlength = this.query.pageNum * this.query.pageSize;
        //
        //
        let resStrLength = 0;
        if (DataAll.length % this.query.pageSize == 0) {
          resStrLength =
            (parseInt(DataAll.length / this.query.pageSize) - 1) *
              this.query.pageSize +
            1;
        } else {
          resStrLength =
            parseInt(DataAll.length / this.query.pageSize) *
              this.query.pageSize +
            1;
        }
        //   
        if (strlength > resStrLength) {
          strlength = resStrLength;
        }
        //   
        if (endlength > DataAll.length) {
          endlength = DataAll.length;
        }
        //
        for (let i = strlength - 1; i < endlength; i++) {
          this.tableData.push(DataAll[i]);
        }
        //
        this.query.total = DataAll.length;
      }
    },
    //
    handleSizeChange(val) {
      console.log(` ${val} `);
      this.query.pageSize = val;
      this.getData();
    },
    //
    handleCurrentChange(val) {
      console.log(`: ${val}`);
      this.query.pageNum = val;
      this.getData();
    },
    //
    handleAdd(scope) {
      this.dialogVisible = true;
      //
      setTimeout(() => {
        this.$bus.$emit("addTable");
      }, 0);
    },
    //
    handleEdit(scope) {
      this.dialogVisible = true;
      //
      setTimeout(() => {
        this.$bus.$emit("editTable", scope.row);
      }, 0);
    },
    handleDelete(index, row) {
      this.$store.commit("bookTable/DeleteElement", row);
      this.getData();
    },
  },
};
</script>
<style scoped>
.wrap {
  width: 100%;
}
.el-pagination {
  margin-top: 15px;
}


</style>
